import React, { useState } from "react";
import PropTypes from "prop-types";
import { PopoutWrapper, FormLayout, Textarea, Button } from "@vkontakte/vkui";

const Ask = ({ question, setQuestion, setPopout }) => {
  const [valid, setValid] = useState(true);
  const submit = () => {
    setValid(question != null);
    if (question != null) {
      setQuestion(question);
      setPopout(null);
    }
  };
  return (
    <PopoutWrapper v="center" h="center">
      <FormLayout style={{ background: "#232323" }}>
        <Textarea
          top="Задайте вопрос шару"
          placeholder={"Я пидор?"}
          value={question}
          onChange={e => (question = e.target.value)}
          status={valid ? "valid" : "error"}
          bottom={valid ? "" : "Пожалуйста, введите вопрос"}
        />
        <Button
          onClick={() => {
            submit();
          }}
        >
          Готово
        </Button>
      </FormLayout>
    </PopoutWrapper>
  );
};
Ask.propTypes = {
  question: PropTypes.string,
  setQuestion: PropTypes.func.isRequired,
  setPopout: PropTypes.func.isRequired
};

export default Ask;
